import { ThemeProvider } from '@mui/system';
import React from 'react';
import ReactDOM from 'react-dom';
import App from './App';
import { theme } from '../theme';
import './index.scss';

ReactDOM.render(
  <React.StrictMode>
    <ThemeProvider theme={theme}>
      <App />
    </ThemeProvider>
  </React.StrictMode>,
  document.getElementById('root')
);
